const dropBtn = document.querySelector(".dropdown-btn");
const dropMenu = document.querySelector(".header-menu");

dropBtn.addEventListener("click", function () {
  dropMenu.classList.toggle("opened");
});

const iphonePrev = document.querySelector(".iphone-prev");
const iphoneNext = document.querySelector(".iphone-next");
const phoneMainBlock = document.querySelector(".iphone");

function iphoneBtnHandler() {
  phoneMainBlock.classList.toggle("change-bg");
}

iphonePrev.addEventListener("click", iphoneBtnHandler);
iphoneNext.addEventListener("click", iphoneBtnHandler);

const swiper3 = new Swiper(".qotes", {
  autoplay: true,
  loop: true,
  slidesPerView: 1.5,
  spaceBetween: 30,
  freeMode: true,
});

const swiper2 = new Swiper(".ipad-device", {
  navigation: {
    prevEl: ".ipad-prev",
  },
  allowSlideNext: false,
  loop: true,
});
